body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Karla", sans-serif;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#959595+0,0d0d0d+46,010101+50,0a0a0a+53,4e4e4e+76,383838+87,1b1b1b+100;Black+Gloss+Pipe */
  background-image: linear-gradient(to right, #c33764, #1d2671);
  /* Add the blur effect */

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  width: 25%;
}
.navbar {
  max-width: 100vw;
}
.nav-theme {
  background: #1d2671; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #c33764,
    #1d2671
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 20px;
}

.animate-navbar {
  box-shadow: 1px 1px 1px #222;
  -webkit-animation: moveDown 0.5s ease-in-out;
          animation: moveDown 0.5s ease-in-out;
}

@-webkit-keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

@keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

.custom-img {
  height: 100vh;
  max-width: 100%;
  object-fit: cover;
}

/* body {
  max-width: 80vw;
} */

.about {
  background-color: transparent;
  font-size: 1.1em;
  font-family: "Helvetica";
}

.profile {
  max-height: 60vh;
}

.my-details {
  background-image: linear-gradient(to right, #f5f7fa, #c3cfe2);
}

@media screen and (max-width: 990px) {
  .profile {
    margin-left: 7vw;
    max-height: 40vh;
  }
  .my-details {
    max-width: 90vw;
    margin: auto;
  }
}

.accordian-main {
  cursor: pointer;
  display: inline-block;
  -webkit-text-decoration: none;
  text-decoration: none;
  font-size: 14px;
  background-color: rebeccapurple;
  border: none;
  border-radius: 4px;

  padding: 10px;
  color: #fff;
}

.list-styles {
  list-style-type: circle;
}

.image-style1 {
  width: 3em;
}

.image-resize {
  width: 15em;
}

.spy-img {
  object-fit: contain;
}

#link-button {
  padding-top: 2em;
}

#buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.img-resize {
  max-width: 10em;
  max-height: 10em;
}

.style {
  font-size: 20px;
}
#xpContainer {
  display: flex;
}
.jumbo-style {
  background: transparent;
  /* background-image: linear-gradient(to right, #c33764, #1d2671); */
}

.card {
  margin-left: 2em;
  margin-right: 2em;
  background: rgba(255, 255, 255, 0.918);
}
.body-title-style {
  font-size: 25px;
  font-weight: 800;
}

@media screen and (max-width: 990px) {
  #xpContainer {
    flex-wrap: wrap;
    margin-left: 0;
  }
  .card {
    margin: auto;
    margin-bottom: 2em;
  }
}

.contact-jumbotron {
  background: transparent;
  /* background-image: url("../../assets/img/carousal/animebg2.png"); */
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 0;
}

#contact {
  margin-top: 4em;
}

.footer-style {
  background-color: #101018f8;
  color: #fff;
  font-size: 1.5em;
  border: 2px solid #101018f8;
  border-top-left-radius: 3px;
  max-width: 100%;
  padding: 0 0 0px !important;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0.75),
    rgba(255, 255, 255, 0)
  );
}

.container-box {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}

.particles-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

html {
  overflow-x: hidden;
}
body {
  position: relative;
}

